import React, { Component } from 'react';

export default class Search extends Component {
    state = {

    }

    handleClick = (e) => {
        console.log('hey')
        console.log(e.target.value)
        const clearSpace = e.target.value.length ? (e.target.value = '') : (console.log('the coast is clear sir...'))
        if (e.target.value == '') {
            this.setState({
                newState: undefined
            })
            document.getElementById('display').innerHTML = ''

        }
    }

    handleChange = (e) => {
        let { dataState } = this.props;
        let appState = dataState.Credential;
        var newState = [];
        for (var x = 0; x < appState.length; x++) {
            if (appState[x].appName == e.target.value) {
                newState.push(appState[x]);
                this.setState({
                    newState
                })
            }
        }
    }


    handleReturn = (appName, username, password) => {
        return (
            (e) => {
                e.preventDefault();
                let liame = prompt('Enter your Email Address: ');
                let pswd = prompt('Enter your password: ');
                if (liame == 'c' && pswd == 'c') {
                    alert(`Username: ${username} \nPassword: ${password} \nApplication: ${appName}`)
                }
                else {
                    alert('Wrong Information,\nPassword will not be shown');

                }
            }
        )
    }
    handleSubmit = (e) => {
        e.preventDefault();
        var searchInput = document.getElementById('search');
        var display_div = document.getElementById('display');
        if (this.state.newState == undefined || searchInput.value == '') {
            console.log('nope');
            display_div.innerHTML = '<h1>&#128557 No Data Found &#128557</h1>';
            display_div.style.color = 'red';
        }
        else {
            this.setState({
                newState: this.state.newState
            })
            e.preventDefault();
            var data = this.state.newState
            searchInput.value = '';
            display_div.innerHTML = '<h1>&#128512 Data Found &#128519 </h1></br>';
            display_div.style.color = 'green';
            data.map((filteredData) => {
                    var newDiv = document.createElement('div');
                    newDiv.className = 'cred-container-inner';
                    newDiv.innerHTML =
                        `<h2>Application: ${filteredData.appName}</h2><br/>
                         <p><b>Email:</b> ${filteredData.email}</br>
                         <b>Username:</b>${filteredData.username}</p>
                         <p><b>Password:</b> <span className='private-password'>
                         <span className='private-password'>
                         <button class='on-password'>on Password</button>`;
                    display_div.appendChild(newDiv)
                    var btns = document.getElementsByClassName('on-password');
                    for(var x = 0; x < btns.length; x++){
                        btns[x].addEventListener('click',this.handleReturn(filteredData.appName,filteredData.username,filteredData.password));
                    }
                
            })

        }
    }
    render() {
        return (
            <div className='search-container'>
                <h1>Search Credential</h1>
                <form onSubmit={this.handleSubmit}>
                    <input type='text' id='search' placeholder='search...' onChange={this.handleChange} onClick={this.handleClick} />
                    <br /><button type='submit' onClick={this.handleSubmit}>Search</button>
                </form>
                <div id='display'>
                    <p>

                    </p>
                </div>
            </div>
        )
    }
}