import React, { Component } from 'react';
import NewCreds from './components/NewCred';
import Search from './components/search';
import './style.css';

class creds {
  constructor(email, username, password, appName) {
    this.email = email;
    this.username = username;
    this.password = password;
    this.appName = appName;

  }
}

export default class App extends Component {
  state = {
    Credential: [
      // new creds('dummy@gmail.com', 'dumbdumb', 'dummmy', 'dummy'),
    ]
  }


  savingStatetoStorage = () => {
    localStorage.setItem("restoreSTATE", JSON.stringify(this.state));
    console.log('data saved');

    setTimeout(() => {
      this.restoreStatetoStorage();
    }, 50)
  }


  restoreStatetoStorage = () => {
    var newCredWarning = document.getElementById('warning');
    var data = JSON.parse(localStorage.getItem("restoreSTATE"));
    if (data == null) {
      newCredWarning.innerHTML = '&#128524 No Previous State Found &#128524';
      newCredWarning.style.color = 'red';
    }
    else {
      newCredWarning.innerHTML = '&#128513 Data Loaded Successfully &#128526';
      newCredWarning.style.color = 'green';
      this.setState({
        Credential: data.Credential
      })
      var display_div = document.getElementById('cred-container');
      display_div.innerHTML = ''
      this.state.Credential.map((x) => {
        var newDiv = document.createElement('div');
        newDiv.className = 'cred-container-inner';
        newDiv.innerHTML =
          `
        <h2>Application: ${x.appName}</h2><br/>
        <p><b>Email:</b> ${x.email}</br>
        <b>Username:</b>${x.username}</p>
        <p><b>Password:</b> <span className='private-password'>
        <span className='private-password'>
        <button class='on-password'>on Password</button>
        `;
        display_div.appendChild(newDiv)

      })
    }


  }



  //need to enter something
  handleReceivedCreds = (ReceivedData) => {
      alert(`Data Received`);
      var InsertCred = new creds(ReceivedData.email, ReceivedData.username, ReceivedData.password, ReceivedData.appName);
      var warning = document.getElementById('new-credential-warning');
      console.log(ReceivedData)
      this.setState({
        Credential: this.state.Credential.concat(InsertCred)
      })
      setTimeout(() => {
        console.log(this.state)
        this.savingStatetoStorage();
        warning.innerHTML = '&#128193	Data Inserted Successfully &#128193';
      }, 50)

  }


  render() {
    return (
      <div id='container' onChange={this.savingStatetoStorage}>
      <nav>PadLock</nav>
        <Search dataState={this.state} savingStatetoStorage={this.savingStatetoStorage} />
        <NewCreds handleReceivedCreds={this.handleReceivedCreds} />
        <h1>My Credentials</h1>
        <h3 className='red' id='warning'></h3>
        <h5>Data not <span className='red'>found?</span></h5>
        <div id='LoadOrSave'>
          {/* <button onClick={this.savingStatetoStorage}>Save Data</button><br/> */}
          <button onClick={this.restoreStatetoStorage}>Load Data</button>
        </div>

        <div id='cred-container'>

          {this.state.Credential.map((data) => {
            return (
              <div className='cred-container-inner' id='h1Link'>
                <h2>Application: {data.appName}</h2><br />
                <p><b>Email:</b> {data.email}</p>
                <p><b>Username:</b>{data.username}</p>
                <p><b>Password:</b> <span className='private-password'>
                  <button onClick={
                    (e) => {
                      e.preventDefault();
                      let liame = prompt('Enter your Email Address: ');
                      let pswd = prompt('Enter your password: ');
                      if (liame == 'c' && pswd == 'c') {
                        alert(`Username: ${data.username} \nPassword: ${data.password} \nApplication: ${data.appName}`)
                      }
                      else {
                        alert('Wrong Information,\nPassword will not be shown');

                      }
                    }
                  }>On Password</button></span></p>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}
